import React, { useRef } from 'react';
import { Button, Form } from 'react-bootstrap';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { useSendPasswordResetEmail, useSignInWithEmailAndPassword } from 'react-firebase-hooks/auth';
import './Login.css';
import auth from '../../../firebase.init';
import SocialLogin from '../SocialLogin/SocialLogin';
import Loading from '../../Shared/Loading/Loading';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import PageTitle from '../../Shared/PageTitle/PageTitle';
import axios from 'axios';

const Login = () => {
  let errorElement;
  
  //for login
  const [
    signInWithEmailAndPassword,
    user,
    loading,
    error,
  ] = useSignInWithEmailAndPassword(auth);

  //for reset password
  const [sendPasswordResetEmail, sending] = useSendPasswordResetEmail(
    auth
  );



  if (error) {
    errorElement = <p className='text-danger'>Error: {error?.message} </p>}

  const location=useLocation();
   let from=location.state?.from?.pathname || "/";

    const emailRef = useRef('');
    const passwordRef = useRef('');
    const navigate=useNavigate();

    const handleSubmit= async event=>{
        event.preventDefault();
        const email=emailRef.current.value;
        const password=passwordRef.current.value;

       await signInWithEmailAndPassword(email,password);

       const {data}=await axios.post('http://localhost:5000/login', {email} )
       //console.log(data);
       localStorage.setItem('accessToken' ,data.accessToken);
       navigate(from, {replace:true});
    }

    const navigateRegister=event=>{
      navigate('/register')
    }
 
    if(user){
     // navigate(from, {replace:true});
    }

    const resetPassword= async ()=>{
      const email=emailRef.current.value;
        if(email){
          await sendPasswordResetEmail(email);
        toast('Sent email');
        }
        else{
          toast('Please enter your email address');
        }
    }

    if(loading || sending){
      return<Loading></Loading>
  }

    return (
     

 <div style={{margin:'0 auto',width:'50%', textAlign:'left'}}>
   <PageTitle title="Login" ></PageTitle>
<h1  className='text-warning text-center mt-5'>Please Login   </h1>
    <Form onSubmit={handleSubmit}>
<Form.Group className="mb-3 " controlId="formBasicEmail">
  <Form.Label >Email address</Form.Label>
  <Form.Control ref={emailRef} type="email" placeholder="Enter email" required />
</Form.Group>

<Form.Group className="mb-3" controlId="formBasicPassword">
  <Form.Label>Password</Form.Label>
  <Form.Control ref={passwordRef} type="password" placeholder="Password" required />
</Form.Group>
<Button variant="warning w-50 mx-auto d-block mb-3" type="submit">
 Login
</Button>
</Form>
{errorElement}
<p>New to Genius car ? <Link to='/register' className=' pe-auto text-decoration-none' onClick={navigateRegister} >Please Register</Link>  </p>
  <p>Forget Password ? <button  className='btn btn-link pe-auto text-decoration-none' onClick={resetPassword} >Reset Password</button>  </p>
 <SocialLogin></SocialLogin>
  </div>
 
     
    );
};

export default Login;
